.phone-card {

    display: grid;
    grid-template-rows: 1fr auto;
    break-inside: avoid;
    width: 100%;
    text-align: center;
    margin-bottom: 15px;
    border-radius: 5%;
    background-color: white;

}

.phone-card:hover {
    box-shadow: 10px 10px 5px gray;
    border-style: gray;
    border-radius: 5%;
    color: black;
    
    transform: scale(1.1);
}



.card-content {

    text-decoration: none;
    color: rgb(0, 0, 0, .55);
    grid-row: 2;
    grid-column: 1;
    background-color: rgba(255, 255, 255, .5);
    padding: .2em .5em;
    justify-self: start;
    width: 100%;
    border-radius: 5%;

}


.card-content img {
    margin: auto;
    margin-bottom: 10px;
    break-inside: avoid;
    max-width: 100%;
    align-items: center;
}

.card-content img :hover{
    color: black;
}

.card-content :hover {
    text-transform: none;
    color: black;
}
.phone-card p :hover {
    color: black;

}