
.ad-form {
    padding-top: 20px;
    text-align: center;
    margin-left: 35%;
    margin-right: 35%;
}

.ad-form button {
    background-color: rgb(212, 7, 7);
    border-radius: 15px;
}


@media only screen and (max-width: 992px) {
    .page-title{
        font-size: 2rem;
    }

    .ad-form {
        margin-left: 30%;
        margin-right: 30%;
        
    }
}

@media only screen and (max-width: 602px){
    .page-title{
        font-size: 1.7rem;
    }

    .ad-form {
        margin-left: 15%;
        margin-right: 15%;
        
    }
}

@media only screen and (max-width: 300px){
    .page-title{
        font-size: 1.5rem;
    }

    .ad-form {
        margin-left: 5%;
        margin-right: 5%;
        
    }
}